/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect } from "react";
import "./index.scss";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

// actions
import { getProjectsWithFilterApi } from "../../../stores/reducers/projectSlice";
import { getAllCitiesApi } from "../../../stores/reducers/citySlice";
// icons
import { CIcon } from "@coreui/icons-react";
// ui
import { CRow, CCol } from "@coreui/react";

// components
import Header from "../Layouts/NewHeader";
import Footer from "../Layouts/NewFooter";

const ProjectsContent = (props) => {
  const history = useHistory();
  const dispatch = useDispatch();
  const { projectsFilter } = useSelector((state) => state.projects);
  const { citiesOfProjects } = useSelector((state) => state.cities);

  const [isFixed, setIsFixed] = useState(false);
  const [indexHover, setIndexHover] = useState(null);
  const [filterIndex, setFilterIndex] = useState(0);

  const handleScroll = (event) => {
    if (window.pageYOffset > 5) {
      setIsFixed(true);
    } else {
      setIsFixed(false);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    dispatch(getAllCitiesApi());
    dispatch(getProjectsWithFilterApi("all"));
  }, [dispatch]);

  const handleLeave = (index) => {
    setIndexHover(null);
  };

  const handleHover = (index) => {
    setIndexHover(index);
  };

  const handleBackTop = () => {
    window.scrollTo(0, 0);
  };

  const viewDetailProject = (id) => {
    history.push(`/projects/${id}`);
  };

  const onSelectFilter = (index, filter) => {
    setFilterIndex(index);
    console.log(filter);
    dispatch(getProjectsWithFilterApi(filter));
  };

  return (
    <div className="projectsPage">
      <Header isFixed={isFixed} />

      <div className="projectsContent">
        <div className="filters">
          <label htmlFor="">Filters: </label>
          <ul>
            <li>
              <a
                className={`${filterIndex === 0 ? "active" : ""}`}
                onClick={() => onSelectFilter(0, "all")}
              >
                All
              </a>
            </li>
            {citiesOfProjects &&
              citiesOfProjects.length > 0 &&
              citiesOfProjects.map((item, i) => (
                <li key={i}>
                  <a
                    className={`${filterIndex === i + 1 ? "active" : ""}`}
                    onClick={() => onSelectFilter(i + 1, item._id)}
                  >
                    {item.city || ""}
                  </a>
                </li>
              ))}
          </ul>
        </div>

        <CRow>
          {projectsFilter &&
            projectsFilter.length > 0 &&
            projectsFilter.map((item, i) => (
              <CCol key={`${i}`} lg={4} md={6} xs={12}>
                <div
                  className={`projectItem ${indexHover === i ? "isHover" : ""}`}
                  onMouseLeave={() => handleLeave(i)}
                  onMouseEnter={() => handleHover(i)}
                  onClick={() => viewDetailProject(item._id)}
                >
                  <div className="viewImage">
                    <img
                      src={`${process.env.REACT_APP_CLOUD_FRONT_URI}${
                        item.picture && item.picture.key
                      }`}
                      alt=""
                    />
                  </div>
                  <h3>{item.name || ""}</h3>
                </div>
              </CCol>
            ))}
        </CRow>
      </div>

      {isFixed && (
        <div className="backTop" onClick={handleBackTop}>
          <CIcon name="cilArrowTop" />
        </div>
      )}

      <Footer />
    </div>
  );
};

export default ProjectsContent;
